.hero{
    /* background-image: radial-gradient(#FFFFFF 50%, #CAC8C8 100%),  url('./hero.png'); */
    background-image: url('./hero.png');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    margin-bottom: 78px;
}

.pretitle{
    text-transform: uppercase;
    margin-bottom: 16px;
    text-align: center;
}

.title{
    font-weight: 600;
    margin: 0;
    margin-bottom: 20px;
    font-size: 52px;
    line-height: 61px;
    text-align: center;
    text-shadow: 0px 5.14068px 5.14068px rgba(0, 0, 0, 0.25);
}

.titleSpan{
    background: linear-gradient(89.95deg, #487FFF -1.65%, #73B5FF 51.06%, #CB76FF 99.96%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    text-shadow: 0px 5.14068px 5.14068px rgba(0, 0, 0, 0.25);
}

.afterTitle{
    font-weight: 500;
    font-size: 18px;
    line-height: 140%;
    color: #E9E9E9;
    text-align: center;
    margin-bottom: 40px;
}
.span {
    margin-left: 32px;
}