.aboutUs{
    margin-bottom: 100px;
}


.pretitle {
    font-weight: 500;
    font-size: 15px;
    line-height: 18px;
    text-align: center;
    text-transform: uppercase;
    color: #AAAAAA;
    margin-bottom: 11px;    
}

.title {
    font-weight: 600;
    margin: 0;
    margin-bottom: 40px;
    font-size: 44px;
    line-height: 48px;
    text-align: center;
}

.level{
    font-weight: 600;
    font-size: 24px;
    line-height: 43px;
    margin-bottom: 20px;
    margin-top: 32px;
}

.list{
    margin-bottom: 33px;
}

.item{
    font-size: 16px;
    line-height: 19px;
    color: #E9E9E9;
}

.item + .item{
    margin-top: 32px;
}

.future{
    font-weight: 500;
    font-size: 35px;
    line-height: 120%;
    letter-spacing: 0.02em;
    margin-top: 100px;
    margin-bottom: 30px;
}

.futureSpan{
    background: linear-gradient(89.95deg, #487FFF -1.65%, #73B5FF 51.06%, #CB76FF 99.96%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.futurePar{
    font-weight: 500;
        font-size: 16px;
        line-height: 150%;
        /* or 24px */
    
        letter-spacing: 0.01em;
        opacity: 0.8;
        margin-bottom: 21px;
}

.readMore{
    color: #7B93FF;
    font-size: 15px;
        line-height: 150%;
        /* identical to box height, or 22px */
    
        letter-spacing: 0.01em;
        margin-bottom: 50px;
}

.formContainer{
    background: radial-gradient(100% 100% at 0% 0%, rgba(48, 48, 48, 0.15) 0%, rgba(255, 255, 255, 0) 100%)
            /* warning: gradient uses a rotation that is not supported by CSS and may not behave as expected */
        ;
        box-shadow: inset -5px -5px 250px rgba(255, 255, 255, 0.02);
        backdrop-filter: blur(14px);
        /* Note: backdrop-filter has minimal browser support */
    
        border-radius: 28px;
        width: 294px;
            height: 180px;
    padding: 20px;
}

.block{
    width: 110px;
        height: 58px;
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 10px;
}

 .firstBlock{
    margin: 0 auto;
        width: 160px;
} 

.secondBlock{
    float: left;
}

.formSecondContainer{
    display: flex;
}

.formContainerBottom{
    background: radial-gradient(100% 100% at 0% 0%, rgba(48, 48, 48, 0.15) 0%, rgba(255, 255, 255, 0) 100%)
            /* warning: gradient uses a rotation that is not supported by CSS and may not behave as expected */
        ;
        box-shadow: inset -5px -5px 250px rgba(255, 255, 255, 0.02);
        backdrop-filter: blur(14px);
        /* Note: backdrop-filter has minimal browser support */
    
        border-radius: 28px;
    padding: 24px 20px 60px 24px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.formContainerBottomDiv{
    display: flex;
}

.formContainerBottomPar{
    text-align: center;
    font-size: 14.4222px;
        line-height: 138%;
}

.consumer{
    text-align: center;
}