button{
    font-weight: 500;
    font-size: 15px;
    line-height: 18px;
    padding: 21px 0; 
    width: 190px;
    color: #ffffff;
    background: linear-gradient(140.6deg, #3E6FF9 -27.18%, #3E53F8 126.06%);
    /* button shadow */   
    box-shadow: 0px 0px 49px rgba(42, 7, 254, 0.4);
    border-radius: 10px;
    border: none;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 auto;
}

